<template>
	<section>
		<div class="main-wrap">
			<h2 class="page-header"></h2>
			<el-table
		    :data="orderList"
		    border
		    style="width: 100%">
				<el-table-column type="index" width="50"></el-table-column>
		    <el-table-column prop="orderId" label="" width="150" sortable></el-table-column>
		    <el-table-column prop="createTime" label="" width="180" sortable :formatter="formatTime"></el-table-column>
		    <el-table-column prop="name" label=""></el-table-column>
		    <el-table-column prop="payer" label="" width="110"></el-table-column>
		   <!--  <el-table-column prop="mobile" label="" width="130"></el-table-column> -->
		    <el-table-column prop="method" label="" width="100" :formatter="formatMethod"></el-table-column>
		    <el-table-column prop="amount" label="" width="120" :formatter="formatAmount"></el-table-column>
		    <el-table-column prop="status" label="" width="90" :formatter="formatStatus"></el-table-column>
		    <el-table-column label="" width="150">
		      <template scope="scope">
		        <el-button
		          size="small"
		          @click="handleShow(scope.$index, scope.row)"></el-button>
		        <el-button
		          size="small"
		          type="danger"
		          @click="handleDelete(scope.$index, scope.row)"></el-button>
		      </template>
		    </el-table-column>
		  </el-table>
		</div>
		<el-dialog :visible.sync="dialogVisible" title="">
			<div class="order-detail">
				<div class="order-item">
					<label></label>
					<span>{{orderObj.orderId}}</span>
				</div>
				<div class="order-item">
					<label></label>
					<span>{{orderObj.name}}</span>
				</div>
				<div class="order-item">
					<label></label>
					<span>{{orderObj.payer}}</span>
				</div>
				<div class="order-item">
					<label></label>
					<span>{{orderObj.method === 1 ? '' : ''}}</span>
				</div>
				<div class="order-item">
					<label></label>
					<span>{{''+orderObj.amount}}</span>
				</div>
				<div class="order-item">
					<label></label>
					<span>{{orderObj.status || ''}}</span>
				</div>
				<div class="order-item">
					<label></label>
					<span>{{this.$moment(orderObj.createTime).format('YYYY-MM-DD HH:mm:ss')}}</span>
				</div>
			</div>
			<div slot="footer">
				<el-button type="primary" @click="dialogVisible = false"></el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
	export default {
		data() {
			return {
				orderList: [],
				orderObj: {},
				dialogVisible: false
			}
		},
		methods: {
			formatMethod (row) {
				return row.method === 1 ? '' : row.method === 2 ? '' : ''
			},
			formatAmount (row) {
				return parseInt(row.amount) ? ''+(row.amount).toFixed(2) : row.amount;
			},
			formatTime (row) {
				return this.$moment(row.createTime).format('YYYY-MM-DD HH:mm:ss')
			},
			formatStatus (row) {
				return ''
			},
			handleShow (index, row) {
				console.log(row)
				this.orderObj = Object.assign({}, row);
				this.dialogVisible = true
			},
			handleDelete (index, row) {
				console.log(index, row)
				this.$confirm('', '', {type: 'warning'})
				.then(() => {
					this.orderList.splice(index, 1)
					this.$message.success('')
					console.log(this.orderList)
					localStorage.setItem('orderList', JSON.stringify(this.orderList))
				}).catch(err => {
					this.$message('')
					console.log(err)
				})
			}
		},
		mounted () {
			this.orderList = JSON.parse(localStorage.getItem('orderList')) || [];
		}
	}
</script>
<style scoped lang="scss">
	.page-header {
		margin-top: 30px
	}
	.order-detail {
		width: 80%;
		margin: auto;
		.order-item {
			display: flex;
			margin: 10px 0;
			label {
				width: 90px;
			}
		}
	}
</style>


