				],
				searchList: ['', '','','','','',''],
			}
		},
		methods: {
			searchEvent(e) {
				e.stopPropagation()
			},
			handleSearch() {
				if(!this.searchContent) return;
				this.loading = true;
				setTimeout(() => {
					this.$notify.warning({
	          title: '',
	          message: ` ${this.searchContent}`,
	        })
	        this.loading = false;
	        this.suggestShow = false;
				}, 2000)
			},
			handleFocus() {
				this.inputWidth = '300px';
				this.suggestShow = this.searchContent ? true : false;
				return false;
			},
			handleBlur() {
				if(this.searchContent) return;
				this.inputWidth = '200px'
			},
			handleInput() {
				this.suggestShow = this.searchContent ? true : false
			},
			suggestSearch(index) {
				let content = this.$refs.suggest[index].innerText.substr(3,)
				this.loading = true;
				setTimeout(() => {
					this.$notify.warning({
	          title: '',
	          message: `${content}`,
	        })
	        this.loading = false
	        this.suggestShow = false
				}, 2000)
			},
			cancelForm() {
				this.$notify.warning({
					title: '',
					message: ''
				})
			},
			submitForm() {
				this.$refs.loginForm.validate(valid => {
					if(valid) {
						let loginUser = this.userList.filter(user => {
							if(user.name === this.loginForm.username) return true;
							return false;
						})
						if(!loginUser[0]) {
							return this.$message.error('')
						}
						if(loginUser[0].password === this.loginForm.password) {
							this.$message.success('')
							this.user = {
								name: this.loginForm.username,
								pass: this.loginForm.password,
								loginTime: this.$moment(new Date()).format('YYYY-MM-DD HH:mm:ss')
							}
							localStorage.setItem('user', JSON.stringify(this.user))
							this.loginForm = {
								username: '',
								password: ''
							}
						} else {
							return this.$message.error('')
						}
					} else {
						console.log('err submit')
					}
				})
			},
			exit() {
				this.$confirm('', '', {type: 'warning'}).then(() => {
					localStorage.removeItem('user')
					this.$notify.success('')
					setTimeout(() => {
						this.user = null;
					}, 2000)
				}).catch(err => {
					this.$message('')
				})
			}
		},
		created () {
			let user = this.user = JSON.parse(localStorage.getItem('user')) || null;
			if(!user) return;
			let duration = (new Date().getTime() - new Date(this.user.loginTime).getTime()) / 1000 / 60;
			if (Math.ceil(duration) > 30) {
				this.$message('')
				setTimeout(() => {
					this.user = null;
					this.loginForm = {
						username: user.name,
						password: user.pass
					}
				}, 2000)
			}
		},
		mounted() {
			document.body.addEventListener('click', (e) => {
				this.suggestShow = false
			})
			let searchInput = document.querySelectorAll('.el-input__inner')[0];
    	searchInput.addEventListener('keydown', (e) => {
    		e.keyCode === 13 && this.handleSearch()
    	})
		}
	}
